var auth=false;
$(document).ready(function(){
    authenticated();//Login
    
});

//Login
function authenticated(){
    if(auth===false){
        if(!($("main").hasClass("hidden"))){
            $('main').addClass("hidden");
        }
        var script=`<form class="form container" method="POST">
                        <fieldset>
                            <legend>Username & Password</legend>

                            <label for="username">Username</label>
                            <input  type="text" name="user[username]" placeholder="Your username" id="username">

                            <label for="password">Password</label>
                            <input type="password" name="user[contra]" placeholder="Your password" id="password">

                        </fieldset>
                        <input id="login-button" type="submit" value="Login">
                        </form>`;
        $('#login-screen').append(script);
        $("#login-button").click(function(e){
            e.preventDefault();
            if($("#username").val()==="" || $("#password").val()===""){
                alert("Username and Password are mandatory")
            }else{
                var user={
                    username: $("#username").val(),
                    password:$("#password").val(),
                }
                var info={
                    action: "Auth",
                    user:user,
                }
                $.post("Router.php",info,function(data){
                    if(JSON.parse(data)===null){
                        $("main").removeClass("hidden");
                        auth=true;
                        $('#login-screen').html("");
                        showFolders();
                        clickFolder();
                        clickActivity();
                    }else{
                        alert(JSON.parse(data)[0]);
                    }
                })
            }
        })
    }
}

function showFolders(){ //Show the list of folders
    const folder= $('#folder');
    folder.click(function(){
        const container= $('#folders-container');
        if(container.hasClass("hidden")){
            container.addClass("visible");
            container.removeClass("hidden");
            const items=$('#items-container');
            const path = $('#list-path');
            if(items.hasClass("visible")){
                items.addClass("hidden");
                items.removeClass("visible");
                path.addClass("hidden");
                path.removeClass("visible");
            }
        }
    })
    if($('#folders-container').hasClass("visible")){
        
        importFolders(); //Call a function to get folders
    }
}

//Get folders from database
function importFolders(){
    const container= $('#list-folders');
    container.html("");
    var info={
        action: "viewFolders",
    }
    $.get("Router.php",info,function(data){
        var result= JSON.parse(data);
        result.forEach(function(folder){
            var script=`<li class="li-folders">
                            <p class="title">${folder["name"]}</p>
                            <input do="View" folderId='${folder["id"]}' type="button" class="folder-delete discret-button" value="View items">
                            <input do="Remove" folderId='${folder["id"]}' type="button" class="folder-delete discret-button" value="Remove">
                        </li>`;
            container.append(script);
        })
    })
}


//Send backend an input to create a folder
function addFolder(){
    var folder={
        name:$('#folderName').val(),
    }
    var info={
        action: "addFolder",
        folder:folder,
    }
    $.post("Router.php",info,function(){
        importFolders();
        $('#folderName').val("");
    }); 
}


//Check where the user clicks in order to call the appropiate function (on folders container)
function clickFolder(){
    const folderContainer= $('#folders-container')
    folderContainer.click(function(e){
        e.preventDefault();
        if(["id"] in e.target.attributes && e.target.attributes["id"].value==="folder-add"){
            addFolder();
        }else{
            if(e.target.attributes["do"].value==="View"){
                viewFolder(e.target.attributes["folderId"].value);
            }else{
                if(e.target.attributes["do"].value==="Remove"){
                        deleteFolder(e.target.attributes["folderId"].value);
                    }
                }
            }
    })
}


//Delete a folder, sending backed the id
function deleteFolder(id){
    var folder={
        "id":id,
    }
    var info={
        action:"deleteFolder",
        folder:folder,
    }
    $.post("Router.php",info,function(){
        importFolders();
    });
}


//Get from database the list of to-do items from the selected folder
function viewFolder(id){
    var folder={
        "id":id,
    }
    var info={
        action: "viewFolder",
        folder:folder,
    }
    $.post("Router.php",info,function(data){
        viewList(JSON.parse(data),id);//Calls the function to get the items
    });
}


//Get the items from de database
function viewList(result){
    list= result[0];
    folderInfo= result[1];
    $('#list-path').text(` > ${folderInfo["name"]}`);
    $('#list-path').attr("idFolder",folderInfo["id"]);
    const container= $('#folders-container');
    if(container.hasClass("visible")){
        container.addClass("hidden");
        container.removeClass("visible"); 
    }
    const items=$('#items-container');
    const path = $('#list-path');
    if(items.hasClass("hidden")){
        items.addClass("visible");
        items.removeClass("hidden");
        path.addClass("visible");
        path.removeClass("hidden");
    }
    const listContainer= $('#list-activities');
    listContainer.html("");
    list.forEach(function(activity){
        var status="";
        if(activity["status"]==1){
            status="checked"
        }
        var script=`<li class="li-items">
                        <form class="activity">
                            <input do="changeStatus"class="check" ${status} type="checkbox" activityStatus='${activity["status"]}' activityId='${activity["id"]}' value="1">
                            <label>${activity["name"]}</label>
                        </form>
                        <input do="Edit" activityId='${activity["id"]}' type="button" class="discret-button" value="Edit">
                    </li>`;
    listContainer.append(script);
    })
}

//Check where the user clicks in order to call the appropiate function (on items container)
function clickActivity(){
    const folderContainer= $('#items-container')
    folderContainer.click(function(e){
        e.preventDefault(e.target.attributes);
        if(["id"] in e.target.attributes && e.target.attributes["id"].value==="activity-add"){
            addActivity($('#list-path').attr(`idFolder`));
        }else{
            if(e.target.attributes["do"].value==="Edit"){
                viewActivity(e.target.attributes["activityId"].value);
            }else{
                if(e.target.attributes["do"].value==="changeStatus"){
                    changeStatus(e.target.attributes["activityId"].value,e.target.attributes["activityStatus"].value);
                }
            }
        }
    })
}

//Send backend an input to create a to-do item in the selected folder
function addActivity(listId){
    var activity={
        "name":$('#activityName').val(),
        "idFolder":listId,
        "status":0,
    }
    var info={
        action:"addActivity",
        activity: activity,
    }
    $.post("Router.php",info,function(d){
        $('#activityName').val("");
        var folder={
            "id":listId,
        }
        info={
            action:"viewFolder",
            folder: folder,
        }
        $.post("Router.php",info,function(data){
            viewList(JSON.parse(data),listId)
        }); 
    }); 
}

//Get the id and name of an activity to show on edit container
function viewActivity(id){
    const items=$('#items-container');
    const path = $('#list-path');
    if(items.hasClass("visible")){
            items.addClass("hidden");
            items.removeClass("visible");
            path.addClass("hidden");
            path.removeClass("visible");
            const edit= $('#editing-activities');
            if(edit.hasClass("hidden")){
                edit.addClass("visible");
                edit.removeClass("hidden");
            }
            $('#folder').addClass("hidden");
            $('#folder').removeClass("visible");
    }
    var activity={
        "id":id,
    }
    var info={
        action:"viewActivity",
        activity:activity,
    }
    const edit= $('#editing-activities')
    $.post("Router.php",info,function(data){
        var item= JSON.parse(data);
        var script=`
                    <h4>Editing "${item['name']}"</h4>
                    <input  id="newName" activityId='${item["id"]}' type="text" placeholder='${item["name"]}'>
                    <button do="Save" type="submit">Save</button>
                    <button do="Cancel" type="submit">Cancel</button>`;
        edit.html(script);
        editActivity(edit,item["idFolder"]);
    })


}

//Send backend the order to change the item status from checked to unchecked and vice versa
function changeStatus(id,status){
    var statusChanged;
    if(status==0){
        statusChanged=1;
    }else{
        statusChanged=0;
    }
    var activity={
        "id":id,
        "status":statusChanged
    }
    var info={
        action:"editActivity",
        activity:activity,
    }
    $.post("Router.php",info,function(){
        var folder={
            "id":$('#list-path').attr(`idFolder`),
        }
        info={
            action:"viewFolder",
            folder:folder,
        }
        $.post("Router.php",info,function(data){
            viewList(JSON.parse(data),$('#list-path').attr(`idFolder`))
        }); 
    })
}

//Send backend a new name of an item to change it in the database
function editActivity(edit,idFolder){
    edit.click(function(e){
        e.preventDefault();
       if(e.target.attributes["do"].value==="Save"){
                var activity={
                "id":$('#newName').attr("activityId"),
                "name":$('#newName').val()
                }
                var info={
                    action:"editActivity",
                    activity:activity,
                }
                $.post("Router.php",info,function(){
                    edit.removeClass("visible");
                    edit.addClass("hidden");
                    $('#folder').addClass("visible");
                    $('#folder').removeClass("hidden");
                    var folder={
                        "id":idFolder,
                    }
                    info={
                        action:"viewFolder",
                        folder:folder,
                    }
                    $.post("Router.php",info,function(data){
                        viewList(JSON.parse(data),idFolder)
                    }); 
                })  
            }else{
                if(e.target.attributes["do"].value==="Cancel"){
                    edit.removeClass("visible");
                    edit.addClass("hidden");
                    $('#folder').addClass("visible");
                    $('#folder').removeClass("hidden");
                    var folder={
                        "id":idFolder,
                    }
                    var info={
                        action:"viewFolder",
                        folder:folder,
                    }
                    $.post("Router.php",info,function(data){
                        viewList(JSON.parse(data),idFolder)
                    });
                }
            }
        })
}




